
html,
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
}
.hidden,
[v-cloak] {
  display: none;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix::after {
  content: "";
  clear: both;
  display: table;
  float: none;
}
.no-margin {
  margin: 0 !important;
}
.no-padding {
  padding: 0 !important;
}
.padding {
  padding: 20px !important;
}
.background-white {
  background-color: #fff !important;
}
.main-container {
  border-radius: 10px;
  position: relative;
}
@media (max-width: 800px) {
.main-container {
    border-radius: 0;
}
}
.main-container.main-container-padded {
  padding: 20px;
}
.content-block {
  border-radius: 10px;
  padding: 20px;
  margin: 0 0 20px 0;
}
.content-block.white-block {
  background-color: #fff;
}
.content-block.padded-block {
  padding: 20px;
}
.content-block.unpadded-block {
  padding: 0;
}
.content-block.header-block {
  margin-top: 20px;
  overflow: hidden;
}
.content-wrapper .content-item {
  border-radius: 10px;
  background-color: #fff;
  margin: 30px 0 0 0;
  overflow: hidden;
  padding: 20px;
  position: relative;
}
@media (max-width: 860px) {
.content-wrapper .content-item {
    margin-top: 20px;
}
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.3s ease;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.slide-top-enter-active,
.slide-top-leave-active {
  transition: all 0.3s ease;
}
.slide-top-enter,
.slide-top-leave-to {
  transform: translateY(-100%);
  opacity: 0;
}
.slide-top-enter {
  transition-delay: 2s;
}
.particles-js-canvas-el {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed;
  pointer-events: none;
  z-index: -1;
}
.rendered-markdown {
  white-space: initial;
  word-wrap: break-word;
}
.rendered-markdown h1 {
  margin: 0;
  padding: 0;
  color: #444;
  margin-bottom: 5px;
}
.rendered-markdown h1,
.rendered-markdown h2,
.rendered-markdown h3,
.rendered-markdown h4,
.rendered-markdown h5,
.rendered-markdown p,
.rendered-markdown a,
.rendered-markdown li {
  color: #444;
}
.rendered-markdown h1:first-child,
.rendered-markdown h2:first-child,
.rendered-markdown h3:first-child,
.rendered-markdown h4:first-child,
.rendered-markdown h5:first-child,
.rendered-markdown p:first-child,
.rendered-markdown a:first-child,
.rendered-markdown li:first-child {
  margin-top: 0;
}
.rendered-markdown h1,
.rendered-markdown h2,
.rendered-markdown h3,
.rendered-markdown h4,
.rendered-markdown h5 {
  font-weight: 300;
}
.rendered-markdown h2 {
  color: #444;
  font-size: 1.3em;
  font-weight: bold;
}
.rendered-markdown p {
  margin: 10px 0;
}
.rendered-markdown a {
  color: #082736 !important;
  text-decoration: underline;
}
.rendered-markdown a:hover {
  color: #082736;
  cursor: pointer;
}
.rendered-markdown blockquote {
  border-left: #082736 3px solid;
  color: #ccc;
  margin: 30px 20px 30px 0px;
  padding-left: 20px;
  font-style: italic;
}
.rendered-markdown pre {
  border-radius: 6px;
  background-color: #e9e9e9;
  max-width: 100%;
  padding: 20px;
  white-space: initial;
  word-wrap: break-word;
}
.rendered-markdown img {
  display: inline-block;
  margin: 10px;
  width: 100%;
  max-width: 160px;
}
.rendered-markdown img[data-4c],
.rendered-markdown .fc-image img,
.rendered-markdown .fc-image > img {
  display: block;
  margin: 0;
  max-width: 100%;
  padding: 0;
  width: 100%;
}
.rendered-markdown .md-bio {
  display: inline-block;
  min-height: 100px;
  overflow: hidden;
  padding: 10px;
  position: relative;
  width: 160px;
}
.rendered-markdown .md-bio img.md-bio--image {
  border-radius: 50%;
  margin: 0;
  padding: 0;
  margin: 0 20px;
  height: 120px;
  width: 120px;
}
.rendered-markdown .md-bio .md-bio--content {
  border-left: #e9e9e9 1px solid;
  padding: 10px;
}
.rendered-markdown .md-bio h5.md-bio--caption {
  margin: 0;
  padding: 0;
  font-size: 1em;
  font-weight: bold;
  height: 20px;
  line-height: 20px;
  margin: 5px 0;
  max-width: 160px;
  overflow: hidden;
  text-align: center;
}
.rendered-markdown .md-bio.md-bio--with-bio {
  border-radius: 6px;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  padding: 10px;
  padding-left: 100px;
  max-width: 100%;
  width: 100%;
}
.rendered-markdown .md-bio.md-bio--with-bio img.md-bio--image {
  margin: 0;
  padding: 0;
  position: absolute;
  top: 10px;
  left: 10px;
  height: 80px;
  width: 80px;
}
.rendered-markdown .md-bio.md-bio--with-bio h5.md-bio--caption {
  margin: 0;
  padding: 0;
  height: 20px;
  max-width: 100%;
  text-align: left;
  width: 100%;
}
.rendered-markdown .md-bio.md-bio--with-bio p.md-bio--bio {
  margin: 0;
  padding: 0;
  color: #666;
  text-align: left;
}
html {
  background-color: #082736;
  transition: background-color 0.6s;
}
html::-webkit-scrollbar {
  display: none;
}
body.disable-scroll {
  overflow: hidden;
  min-height: 100%;
  max-height: 100vh;
  padding-bottom: 1px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.main-page .col {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  padding: 0;
}
.main-page .col .container {
  padding: 20px;
}
.main-page .col .container h1 {
  margin: 0;
  padding: 0;
  color: #444;
  margin-bottom: 5px;
}
.main-page .col#col-main {
  border-radius: 10px;
  margin: 0 auto 80px auto;
  max-width: 780px;
  padding-top: 60px;
  position: relative;
  width: calc(100% - 20px);
}
@media (max-width: 800px) {
.main-page .col#col-main {
    margin-bottom: 0;
}
}
#content-overlay {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0,0,0,0);
  pointer-events: none;
  position: fixed;
  transition: background-color 0.3s;
  z-index: 55;
}
#content-overlay.visible {
  background-color: rgba(0,0,0,0.8);
  pointer-events: all;
}
html.primary #navigation {
  background-color: #082736;
}
html.home #navigation {
  background-color: #fff;
}
html.registration #navigation {
  background-color: #161616;
}
html.survey #navigation {
  background-color: #507588;
}
html.schedule #navigation {
  background-color: #082736;
}
html.homework #navigation {
  background-color: #fd3c51;
}
html.fourcorners #navigation {
  background-color: #35bbe9;
}
html.profile #navigation {
  background-color: #082736;
}
.warning-banner {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transition: all 0.3s ease;
  background: #fd3c51;
  bottom: auto;
  color: #fff;
  height: 60px;
  line-height: 60px;
  position: fixed;
  text-align: center;
}


